}

/* error code */
    #error {
    margin-bottom: 0;
    border: 1px solid #333;
    margin-left: 10px;
}

#error ul {
    list-style: square;
    padding: 5px;
    font-size: 11px;
}

#error ul li {
    list-style-position: inside;
    line-height: 1.6em;
}

#error ul li strong {
    color: #e46c6d;
}

#error.valid ul li strong {
    color: #ccc;
    font-weight: bold;
}
