/******* FORM *******/
#frmContact { padding: 30px 15px; }

#frmContact label { display: block; color: #797979; font-weight: 700; line-height: 1.4em; }

#frmContact input { width: 220px; padding: 6px; color: #949494; font-family: Arial, Verdana, Helvetica, sans-serif; font-size: 11px; border: 1px solid #cecece; }

#frmContact input.error { background: #ccc; border-color: #333; }

#frmContact textarea { width: 550px; height: 80px; padding: 6px; color: #adaeae; font-family: Arial, Verdana, Helvetica, sans-serif; font-style: italic; font-size: 12px; border: 1px solid #cecece; }

#frmContact textarea.error { background: #ccc; border-color: #333; }

#frmContact div { margin-bottom: 15px; }

#frmContact div span { margin-left: 10px; color: #b1b1b1; font-size: 11px; font-style: italic; }

#frmContact div span.error { color: #e46c6e; }

#frmContact #send { color: #000; font-weight: 700; font-style: normal; border: 0; cursor: pointer; background-color: #ccc; }

#frmContact #send:hover { background: aqua; }

/* error code */
#error { margin-bottom: 0; border: 1px solid #333; margin-left: 10px; }

#error ul { list-style: square; padding: 5px; font-size: 11px; }

#error ul li { list-style-position: inside; line-height: 1.6em; }

#error ul li strong { color: #e46c6d; }

#error.valid ul li strong { color: #ccc; font-weight: bold; }

