/*=============================================
=                Media Queries                =
=============================================*/
@media (max-width: 1200px) { .search-container { display: none; }
  .aside-box p { width: 100%; } }

@media (max-width: 767px) { img.facebook-icon { display: none; }
  .donate { display: none; }
  .sidebar-background { display: none; }
  .footer-border { border-right: none; }
  .cert-logos { text-align: center; padding: 0px; }
  .get-involved { display: inline; }
  .aside-box p { width: 80%; }
  .facebook-button { display: none; }
  li { text-align: center; }
  .navbar-nav > li { display: block; }
  .navbar-nav > li > a { width: 50%; margin: auto; } }

@media (min-width: 768px) { .sub-menu { display: none; position: absolute; background: #e7e7e7; padding: 10px 10px; width: 150px; z-index: 99; }
  .sub-menu a { background-color: #e7e7e7; }
  li:hover .sub-menu { display: block; } }

@media screen and (max-width: 535px) { .facebook-icon { display: none; } }

@media print { * { background: transparent !important; color: black !important; text-shadow: none !important; filter: none !important; -ms-filter: none !important; }
  a { color: #444 !important; text-decoration: underline; }
  a:visited { color: #444 !important; text-decoration: underline; }
  a[href]:after { content: " (" attr(href) ")"; }
  abbr[title]:after { content: " (" attr(title) ")"; }
  .ir a:after { content: ""; }
  a[href^="javascript:"]:after { content: ""; }
  a[href^="#"]:after { content: ""; }
  pre { border: 1px solid #999; page-break-inside: avoid; }
  blockquote { border: 1px solid #999; page-break-inside: avoid; }
  thead { display: table-header-group; }
  tr { page-break-inside: avoid; }
  img { page-break-inside: avoid; }
  @page { margin: 0.5cm; }
  p { orphans: 3; widows: 3; }
  h2 { orphans: 3; widows: 3; page-break-after: avoid; }
  h3 { orphans: 3; widows: 3; page-break-after: avoid; } }

